import { FC } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Button, ButtonSize } from "common/components/new/Button";
import { Picture } from "common/components/Picture";

import { TeensMainProps } from "./TeensMain.types";
import styles from "./TeensMain.module.scss";

const cx = classNames.bind(styles);

export const TeensMain: FC<TeensMainProps> = ({
    title,
    subtitle,
    ctaButton,
    bracketsVsImpress,
    advantages,
    className,
    onButtonClick,
    buttonLink,
}) => {
    const { brackets, impress } = bracketsVsImpress;

    const renderAdvantage = (item: string, index: number) => (
        <li className={cx("advantages-item")} key={`advantages-item-${index}`}>
            {item}
        </li>
    );

    const buttonAction = onButtonClick
        ? { onClick: onButtonClick }
        : { href: buttonLink };

    return (
        <section className={cx("root", className)}>
            <div className={cx("mobile-galery")}>
                <div
                    className={cx(
                        "image-with-badge",
                        "image-with-badge_mobile-1"
                    )}
                >
                    <span className={cx("badge-text", "badge-text_has-accent")}>
                        {impress.badgeText}
                    </span>
                    <Picture
                        className={cx("mobile-image")}
                        mobile={impress.image.mobile}
                    />
                </div>
                <div
                    className={cx(
                        "image-with-badge",
                        "image-with-badge_mobile-2"
                    )}
                >
                    <span className={cx("badge-text")}>
                        {brackets.badgeText}
                    </span>
                    <Picture
                        className={cx("mobile-image")}
                        mobile={brackets.image.mobile}
                    />
                </div>
            </div>
            <div className={cx("first-row")}>
                <div className={cx("desktop-galery")}>
                    <div
                        className={cx(
                            "image-with-badge",
                            "image-with-badge_desktop"
                        )}
                    >
                        <span
                            className={cx(
                                "badge-text",
                                "badge-text_has-accent"
                            )}
                        >
                            {impress.badgeText}
                        </span>
                        <Picture desktop={impress.image.desktop} />
                        <div className={cx("wave-background")} />
                    </div>
                </div>
                <div className={cx("content")}>
                    <Heading
                        type={HeadingType.S}
                        tag={HeadingTag.H3}
                        className={cx("subtitle")}
                    >
                        {subtitle}
                    </Heading>
                    <Heading tag={HeadingTag.H1} className={cx("title")}>
                        {title}
                    </Heading>
                    <Button
                        size={ButtonSize.M}
                        className={cx("only-desktop", "button")}
                        {...buttonAction}
                    >
                        {ctaButton}
                    </Button>
                </div>
            </div>
            <div className={cx("second-row")}>
                <div className={cx("advantages")}>
                    <div className={cx("image-with-badge")}>
                        <span className={cx("badge-text")}>
                            {brackets.badgeText}
                        </span>
                        <Picture
                            className={cx("advantages-image")}
                            desktop={brackets.image.desktop}
                        />
                    </div>
                    <ul className={cx("advantages-list")}>
                        {advantages.map(renderAdvantage)}
                    </ul>
                </div>
            </div>
        </section>
    );
};
